import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { berhasil, gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
import FormSaldoAwalHutang from "./Component/FormSaldoAwalHutang";

const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
class SaldoAwalHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
    };
  }
  sendData(data) {
    var supplier = data.kodeSupplier ? data.kodeSupplier : "DEFAULT|DEFAULT";
    var split = supplier.split("|");
    var isi = {
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      noBon: this.props.noFaktur,
      kodeSupplier: split[0],
      namaSupplier: split[1],
      tipeTransaksi: "SALDO-AWAL-HUTANG",
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
      footerItem: {
        totalP: 0,
        totalK: 0,
        totalpkg: 0,
        totalNettoExt:
          data.totalHutangGr === undefined
            ? data.totalTitipGr
            : data.totalHutangGr,
        totalBruto: data.totalBruto || 0,
        totalNetto:
          data.totalHutangGr === undefined
            ? data.totalTitipGr
            : data.totalHutangGr,
        totalGross: 0,
        totalQty: 0,
        totalRp:
          data.totalHutangRp === undefined
            ? data.totalTitipRp
            : data.totalHutangRp,
      },
    };
    Axios.post(server + "spp/dc/trm", isi)
      .then(() => berhasil("Saldo Awal Berhasil Disimpan"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => this.props.dispatch(getUUID()))
      .catch(() =>
        gagal("Sepertinya koneksi anda bermasalah, Mohon periksa koneksi anda")
      );
  }

  setTipe(e) {
    this.setState({
      tipeTransksi: e.target.value,
    });
    this.props.dispatch(reset("FormSaldoAwalHutang"));
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/saldoawal">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Saldo Awal Hutang" />
              <div className="card-body">
                <div className="col-lg-4">
                  <label htmlFor="">TIPE TRANSAKSI</label>
                  <select
                    className="form-control"
                    onChange={(e) => this.setTipe(e)}
                  >
                    <option value="HUTANG" key="HUTANG">
                      HUTANG
                    </option>
                    <option value="TITIP" key="TITIP">
                      TITIP
                    </option>
                  </select>
                </div>
                <div className="col-lg-8"></div>
                <div className="col-lg-12 mt-3">
                  <FormSaldoAwalHutang
                    onSubmit={(data) => this.sendData(data)}
                    tipe={this.state.tipeTransksi}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

SaldoAwalHutang = reduxForm({
  form: "SaldoAwalHutang",
  enableReinitialize: true,
})(SaldoAwalHutang);
export default connect(maptostate, null)(SaldoAwalHutang);
